/* TopVideo.css */
.video-container {
  position: relative;
  width: 100%;
  height: 400px; /* Set a fixed height for the banner */
  overflow: hidden;
}

.video-container video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Cover the banner with the video */
}

.text-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); /* Semi-transparent black background */
  color: red;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.heading {
  font-size: 60px;
  font-weight: bold;
  margin-bottom: 10px;
  line-height: 1.2;
}

.paragraph {
  font-size: 25px;
  margin-bottom: 10px;
}

.small-paragraph {
  font-size: 14px;
}

/* TopVideo.css */
/* ... (previous styles) */

@media (max-width: 768px) {
  .video-container {
    height: 300px; /* Reduce the height for smaller screens */
  }

  .video-container video {
    height: 100%; /* Adjust the video height */
  }

  .text-overlay {
    height: 100%; /* Adjust the overlay height */
    max-width: 100%; /* Limit the maximum width of the overlay */
  }

  .heading {
    font-size: 24px; /* Reduce font size for smaller screens */
    max-height: 50px; /* Limit the maximum height of the heading */
    overflow: visible; /* Hide overflow text */

  }
  .paragraph {
    font-size: 16px;
    max-height: 50px; /* Limit the maximum height of the heading */
    overflow: hidden; /* Adjust the font size for smaller screens */
  }
}

